import { useEffect } from "react";

import "bootstrap/dist/css/bootstrap.min.css";

import Container from "react-bootstrap/Container";

import Table from "react-bootstrap/Table";

import Row from "react-bootstrap/Row";

import Col from "react-bootstrap/Col";

import { run as runHolder } from "holderjs/holder";

import NumberFormat from "react-number-format";

import React, { useState } from "react";

import "./pages.css";

export default function MioLeaders() {
  const [mioLeaders, initMioLeaders] = useState([]);
  const [mio10Leaders, initMio10Leaders] = useState([]);
  const [mio100Leaders, initMio100Leaders] = useState([]);

  const fetchData = async () => {
    const response = await fetch(
      "https://wax.api.atomicassets.io/atomicassets/v1/accounts?collection_name=upliftium.hi&schema_name=upliftium&page=1&limit=50&order=desc"
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response.json();
    }
  };
  useEffect(() => {
    fetchData()
      .then((res) => {
        const mioholderArray = [];

        let mioCount = 0;

        res.data.forEach((holder) => {
          if (
            holder.account !== "y3gnq.wam" &&
            holder.account !== "1mil.lft.3" &&
            holder.account !== "upliftservic" &&
            holder.account !== "wesaveuplift"
          ) {
            mioholderArray[mioCount] = holder;
            mioCount++;
          }
        });

        initMioLeaders(mioholderArray);
      })
      .catch((e) => {
        console.log(e.message);
      });
  }, []);

  const fetch10million = async () => {
    const response = await fetch(
      "https://wax.api.atomicassets.io/atomicassets/v1/accounts?template_id=583334&page=1&limit=100&order=desc"
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response.json();
    }
  };

  useEffect(() => {
    fetch10million()
      .then((res) => {
        const mioholderArray = [];

        let mioCount = 0;

        res.data.forEach((holder) => {
          if (
            holder.account !== "y3gnq.wam" &&
            holder.account !== "1mil.lft.3" &&
            holder.account !== "upliftservic" &&
            holder.account !== "wesaveuplift"
          ) {
            mioholderArray[mioCount] = holder;
            mioCount++;
          }
        });

        initMio10Leaders(mioholderArray);
      })
      .catch((e) => {
        console.log(e.message);
      });
  }, []);

  const fetch100million = async () => {
    const response = await fetch(
      "https://wax.api.atomicassets.io/atomicassets/v1/accounts?template_id=583323&page=1&limit=100&order=desc"
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response.json();
    }
  };

  useEffect(() => {
    fetch100million()
      .then((res) => {
        const mioholderArray = [];

        let mioCount = 0;

        res.data.forEach((holder) => {
          if (
            holder.account !== "y3gnq.wam" &&
            holder.account !== "1mil.lft.3" &&
            holder.account !== "upliftservic" &&
            holder.account !== "wesaveuplift"
          ) {
            mioholderArray[mioCount] = holder;
            mioCount++;
          }
        });

        initMio100Leaders(mioholderArray);
      })
      .catch((e) => {
        console.log(e.message);
      });
  }, []);

  useEffect(() => {
    runHolder("image-class-name");
  });
  console.log(mioLeaders);
  return (
    <Container fluid>
      <Container>
        <Row>
          <Col
            style={{
              margin: "auto",
              width: "20em",
              alignContent: "top",
              padding: "0",
              paddingRight: "0.5em",
              marginTop: "0",
            }}
            xl={1}
          >
            <Row style={{ height: "1em" }}></Row>
            <Row
              style={{
                backgroundColor: "rgb(6, 255, 255)",
                borderRadius: "0.75em",
                border: "solid",
                borderColor: "rgb(255, 65, 198, 0.5)",
                padding: "0em",
              }}
            >
              <Col
                style={{ fontSize: "1.5em", padding: "0em", fontWeight: "900" }}
              >
                1 Million NFT Leaders
              </Col>

              <Table variant="custom" responsive>
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Account</th>
                    <th>Total</th>
                  </tr>
                </thead>
                <tbody>
                  {[...mioLeaders]
                    .sort((a, b) => b.assets - a.assets)
                    .slice(0, 25)
                    .map((item, idx) => {
                      //let amount = item[1].toLocalString();
                      return (
                        <tr key={idx + "rank"}>
                          <td>{idx + 1}</td>
                          <td>
                            <a
                              href={
                                "https://wax.atomichub.io/profile/" +
                                item.account +
                                "?collection_name=upliftium.hi&order=desc&sort=transferred"
                              }
                              target="_blank"
                              rel="noreferrer"
                            >
                              {item.account}
                            </a>
                          </td>
                          <td>
                            <NumberFormat
                              thousandSeparator={true}
                              displayType={"text"}
                              value={item.assets}
                              decimalScale={2}
                            />
                          </td>
                        </tr>
                      );
                    })}
                </tbody>
              </Table>
            </Row>
            <Row style={{ height: "1em" }}></Row>
            <Row
              style={{
                backgroundColor: "rgb(6, 255, 255)",
                borderRadius: "0.75em",
                border: "solid",
                borderColor: "rgb(255, 65, 198, 0.5)",
                padding: "0em",
              }}
            >
              <Col
                style={{ fontSize: "1.5em", padding: "0em", fontWeight: "900" }}
              >
                10 Million NFT Leaders
              </Col>

              <Table variant="custom" responsive>
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Account</th>
                    <th>Amount</th>
                  </tr>
                </thead>
                <tbody>
                  {[...mio10Leaders]
                    .sort((a, b) => b.assets - a.assets)
                    .slice(0, 25)
                    .map((item, idx) => {
                      //let amount = item[1].toLocalString();
                      return (
                        <tr key={idx + "rank"}>
                          <td>{idx + 1}</td>
                          <td>
                            <a
                              href={
                                "https://wax.atomichub.io/profile/" +
                                item.account +
                                "?collection_name=upliftium.hi&order=desc&sort=transferred"
                              }
                              target="_blank"
                              rel="noreferrer"
                            >
                              {item.account}
                            </a>
                          </td>
                          <td>
                            <NumberFormat
                              thousandSeparator={true}
                              displayType={"text"}
                              value={item.assets}
                              decimalScale={2}
                            />
                          </td>
                        </tr>
                      );
                    })}
                </tbody>
              </Table>
            </Row>
            <Row style={{ height: "1em" }}></Row>
            <Row
              style={{
                backgroundColor: "rgb(6, 255, 255)",
                borderRadius: "0.75em",
                border: "solid",
                borderColor: "rgb(255, 65, 198, 0.5)",
                padding: "0em",
              }}
            >
              <Col
                style={{ fontSize: "1.5em", padding: "0em", fontWeight: "900" }}
              >
                100 Million NFT Leaders
              </Col>

              <Table variant="custom" responsive>
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Account</th>
                    <th>Amount</th>
                  </tr>
                </thead>
                <tbody>
                  {[...mio100Leaders]
                    .sort((a, b) => b.assets - a.assets)
                    .slice(0, 25)
                    .map((item, idx) => {
                      //let amount = item[1].toLocalString();
                      return (
                        <tr key={idx + "rank"}>
                          <td>{idx + 1}</td>
                          <td>
                            <a
                              href={
                                "https://wax.atomichub.io/profile/" +
                                item.account +
                                "?collection_name=upliftium.hi&order=desc&sort=transferred"
                              }
                              target="_blank"
                              rel="noreferrer"
                            >
                              {item.account}
                            </a>
                          </td>
                          <td>
                            <NumberFormat
                              thousandSeparator={true}
                              displayType={"text"}
                              value={item.assets}
                              decimalScale={2}
                            />
                          </td>
                        </tr>
                      );
                    })}
                </tbody>
              </Table>
            </Row>
          </Col>
         
          <Col>
            <neftyblocks-market
              collection="upliftium.hi"
              limit="10"
            ></neftyblocks-market>
          </Col>
        </Row>
 
      </Container>
      
    </Container>
  );
}
