import { useEffect } from "react";
import "@neftyblocks/market";
import "bootstrap/dist/css/bootstrap.min.css";

import Container from "react-bootstrap/Container";
import Table from "react-bootstrap/Table";
import Orders from "./rewards/orders";

import Row from "react-bootstrap/Row";

import Col from "react-bootstrap/Col";

import { run as runHolder } from "holderjs/holder";

import React, { useState } from "react";

import "./pages.css";

export default function NewPlotBonus() {
  const [purchaseLeaders, initLiftLeaders] = useState([]);


  const fetchData = async () => {
    const response = await fetch(
      "https://wax.api.atomicassets.io/atomicmarket/v2/sales?state=3&seller=upliftservic&page=1&limit=100&order=desc&sort=created"
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response.json();
    }
  };
  useEffect(() => {
    fetchData()
      .then((res) => {
        //console.log(res.data);
        let tempArray = [];

        let purchaseCount = 0;

        const purchaseObject = {
          account: "",
          time: "",
          pcount: 0,
        };
        //console.log(res.data);
        //let listp = res.data.filter((obj) => obj.assets[0].transferred_at_time < 1662757642500);
        // console.log(listp);

        res.data.forEach((purchase) => {
          const p_temp = Object.create(purchaseObject);
          //console.log(+purchase.assets[0].transferred_at_time);
          let time = new Date(+purchase.assets[0].transferred_at_time);
          //console.log(time);
          p_temp.account = purchase.buyer;
          p_temp.time = time.toUTCString();
          const count = res.data.filter(
            (obj) => obj.buyer === purchase.buyer
          ).length;
          p_temp.pcount = count;

          const found = tempArray.find(
            (element) => element.account === purchase.buyer
          );

          if (!found) {
            tempArray[purchaseCount] = p_temp;
            purchaseCount++;
          }
        });

        tempArray.sort((a, b) => b.pcount - a.pcount);

        //console.log(tempArray);
        initLiftLeaders(tempArray);
        //console.log("Sep 08 2022".getTime());
      })
      .catch((e) => {
        console.log(e.message);
      });
      let totalAmountMios = 0;
      Orders.data.payload.orders.sort((a, b) => b.account.chainAccount - a.account.chainAccount);
      console.log(Orders.data.payload.orders);
      Orders.data.payload.orders.forEach(order=>{
        //console.log(order.total_price);
        totalAmountMios +=order.total_price; 
      })
      console.log(totalAmountMios/1000000);
      
  }, []);

  useEffect(() => {
    runHolder("image-class-name");
  });

  return (
    <Container fluid>
      <Container>
        <Row>
          <Col
            style={{
              margin: "auto",
              width: "29em",
              alignContent: "top",
              padding: "0",
              paddingRight: "0em",
              marginTop: "0.5em"
            }}
            xl={1}
          >
            <Row
              style={{
                backgroundColor: "rgb(6, 255, 255)",
                borderRadius: "0.75em",
                border: "solid",
                borderColor: "rgb(255, 65, 198, 0.5)",
                padding: "0em",
              }}
            >
              <Col
                style={{ fontSize: "1.5em", padding: "0em", fontWeight: "900" }}
              >
                New Plot Purchase Leaders (September)
                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  Every 5 Plots Purchased Earns 100 Shards
                </div>
              </Col>
              <Table variant="custom" responsive>
                <thead>
                  <tr>
                    <th>Account</th>

                    <th>Purchase Count</th>
                    <th>Bonus Shards</th>
                  </tr>
                </thead>
                <tbody>
                  {purchaseLeaders.map((item, idx) => {
                    //let amount = item[1].toLocalString();
                    return (
                      <tr key={idx + "rank"}>
                        <td>{item.account}</td>

                        <td>{item.pcount}</td>
                        <td>{Math.floor(item.pcount / 5) * 100}</td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
            </Row>
          </Col>

          <Col
            style={{
              margin: "auto",
              width: "30em",
              alignContent: "top",
              padding: "0",
              paddingRight: "0.1em",
              marginTop: "0.5em",
            }}
            xl={1}
          >
            <Row
              style={{
                backgroundColor: "rgb(6, 255, 255)",
                borderRadius: "0.75em",
                border: "solid",
                borderColor: "rgb(255, 65, 198, 0.5)",
                padding: "0em",
              }}
            >
              <Col
                style={{ fontSize: "1.5em", padding: "0em", fontWeight: "900" }}
              >
                <div
                  style={{
                    fontSize: "1.2em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
           How to buy a NEW wax plot ...
                </div>
                <div
                  style={{
                    fontSize: "1.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  Go to{" "}
                  <a
                    href={"https://maps.theuplift.world/"}
                    target="_blank"
                    rel="noreferrer"
                    style={{
                      fontSize: "1em",
                      padding: "0em",
                      fontWeight: "900",
                      color: "rgb(255, 65, 198, 0.5)",
                    }}
                  >
                    World Map
                  </a>
                </div>
                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  Toggle the 'Plots' layer to show borders
                </div>

                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  <img
                    alt="toggle plot borders"
                    style={{
                      width: "20em",
                      borderRadius: "20px",
                      padding: "0.5em",
                    }}
                    src={require("./img/Screenshot 2022-09-17 062852.jpg")}
                  />
                </div>
                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  Find any blue border plot owned by 'Upliftservic'
                </div>
                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  <img
                    alt="toggle plot borders"
                    style={{
                      width: "20em",
                      borderRadius: "20px",
                      padding: "0.5em",
                    }}
                    src={require("./img/Screenshot 2022-09-17 062958.jpg")}
                  />
                </div>

                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  Click the link to go to listing at Atomichub
                </div>
                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  <img
                    alt="toggle plot borders"
                    style={{
                      width: "20em",
                      borderRadius: "20px",
                      padding: "0.5em",
                    }}
                    src={require("./img/Screenshot 2022-09-17 063059.jpg")}
                  />
                </div>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
    </Container>
  );
}
