import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import { NavLink } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./nav.css";

function NavigationBar() {
  return (
    <Navbar expand="lg" bg="bg" variant="dark">
      <Container>
        <Navbar.Brand as={NavLink} to="/rewards">
          <img
            alt="mio nft and liftium"
            className="nav-img"
            src={require("./img/eco.png")}
          />
          <span id="brand-tag">Uplift Community Project</span>
        </Navbar.Brand>

        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
         
            
            <NavDropdown title="Mio/Liftium Stats" id="basic-nav-dropdown">
              <NavDropdown.Item as={NavLink} to="/liftium">
                Liftium Leaders
              </NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/mio">
                Mio Leaders
              </NavDropdown.Item>
            </NavDropdown>
            <Nav.Link style={{}} as={NavLink} to="/newplotbonus">
             New Plot Bonus
            </Nav.Link>
            <Nav.Link style={{}} as={NavLink} to="/dripleaders">
              Drip Leaders
            </Nav.Link>
            <Nav.Link style={{}} as={NavLink} to="/landleaders">
             Land Key Leaders
            </Nav.Link>
            <Nav.Link as={NavLink} to="/stores">
              Item Prices/Locations
            </Nav.Link>
          
            <Nav.Link as={NavLink} to="/mtleaders">
              MT Leaders
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default NavigationBar;
