import { useCallback, useEffect, useMemo } from "react";

import Container from "react-bootstrap/Container";

import Table from "react-bootstrap/Table";

import Row from "react-bootstrap/Row";

import Col from "react-bootstrap/Col";

import React, { useState } from "react";

import "bootstrap/dist/css/bootstrap.min.css";
import "./pages.css";

import Caps from "./rewards/points_action_block";

export default function RewardCaps() {
  console.log(Caps);
  let capArray = [];
  let specialBlockArray = [];
  let ca = 0;

  const specialBlock = {
    name: "",
    action: "",
    value: 0,
  };

  const capObject = {
    type: "",
    cap: 0,
    per: 0,
    points: 0,
  };

  capArray.length = 0;
  let i = 0;

  Object.entries(Caps).forEach(([key, value]) => {
    const cap_temp = Object.create(capObject);
    //console.log(`${key}`);
    cap_temp.type = key.replace('player-','').replace('move-','').replace('vehicle-','Travel using a ');


    Object.entries(value).forEach(([k, v]) => {
      //console.log(k);
      
      if (k === "_per") {
        cap_temp.per = v;
      } else if (k === "_cap") {
        cap_temp.cap = v;
      } else if (k === "_points") {
        cap_temp.points = v;
      } else {
        const temp_block = Object.create(specialBlock);

        temp_block.name = k;
        temp_block.value = v;
        temp_block.action = key;
        if(k!=='EMERALD_BLOCK'){
        specialBlockArray[i] = temp_block;
       i++
      }

      
      }
    });

    capArray[ca] = cap_temp;
    ca++;
  });

  useEffect(() => {
    console.log(capArray);
    console.log(specialBlockArray);

  });

  return (
    <Container fluid>
      <Row s={8}>
        <Col >
          <Row
            style={{
              backgroundColor: "rgb(6, 255, 255)",
              borderRadius: "0.75em",
              border: "solid",
              borderColor: "rgb(255, 65, 198, 0.5)",
              padding: "0em",
            }}
          >
            <Col
              style={{ fontSize: "2.5em", padding: "0em", fontWeight: "900" }}
            >
              Point Scoring System
            </Col>
            <Table variant="custom" responsive>
              <thead></thead>

              <thead>
                <tr>
                  <th>Action</th>
                  <th>Point Cap</th>
                  <th>Points per unit</th>
                  <th>Unit Size (blocks)</th>
                  <th>Total Blocks</th>
                </tr>
              </thead>
              <tbody>
                {[...capArray].map((item, idx) => {
                  return (
                    <tr key={idx + "rank"} style={{ verticalAlign: "middle" }}>
                      <td>{item.type}</td>
                      <td>{item.cap}</td>
                      <td>{item.points} </td>
                      <td>{item.per}</td>
                      <td>{(item.cap / item.points) * item.per}</td>
                    </tr>
                  );
                })}
                   <tr  style={{ verticalAlign: "middle" }}>
                      <td colSpan="5" style={{fontSize:"1.5em"}} >Note: 10k Cap for each of the special burn items rotten flesh, emerald, lapis, and gold block.  10k cap for ALL other items combined.</td>
                    </tr>
              </tbody>
            </Table>
          </Row>
        </Col>
      


        <Col>
          <Row
            style={{
              backgroundColor: "rgb(6, 255, 255)",
              borderRadius: "0.75em",
              border: "solid",
              borderColor: "rgb(255, 65, 198, 0.5)",
              padding: "0em",
            }}
          >
            <Col
              style={{ fontSize: "2.5em", padding: "0em", fontWeight: "900" }}
            >
              Special Blocks/actions
            </Col>
            <Table variant="custom" responsive>
              <thead></thead>

              <thead>
                <tr>
                  <th>Action</th>
                  <th>Block Type</th>
                  <th>Points per block</th>
                </tr>
              </thead>
              <tbody>
                {[...specialBlockArray].map((item, idx) => {
                  return (
                    <tr key={idx + "rank"} style={{ verticalAlign: "middle" }}>
                      <td>{item.action}</td>
                      <td>{item.name}</td>
                      <td>{item.value} </td>
                    </tr>
                  );
                })}
              </tbody>
            </Table>
          </Row>
        </Col>


      </Row>
    </Container>
  );
}
