import { useEffect } from "react";

import Players from "./rewards/playerRewardDetailsS8W1";
import Plots from "./rewards/plotRewardDetailsS8W1";
import Burn from "./rewards/burnRewardDetailsS8W1";

import "bootstrap/dist/css/bootstrap.min.css";

import Container from "react-bootstrap/Container";

import Row from "react-bootstrap/Row";

import Col from "react-bootstrap/Col";

import Table from "react-bootstrap/Table";

import NumberFormat from "react-number-format";

import React, { useState } from "react";

import "./pages.css";
import { isNullOrUndefined } from "holderjs";
import { isUndefined } from "holderjs";

export default function rewardDetailsS8W1() {
  let playerRewards = [];
  let pr = 0;

  let top_100 = 0;

  let plotRewards = [];
  let plotr = 0;

  let burnRewards = [];
  let burnr = 0;

  const playerObject = {
    account: "",
    p_id: "",
    p_name: "",
    amount: 0,
    type: "",
    url_skin: "",
    walking: 0,
    flying: 0,
    gliding: 0,
    swimming: 0,
    swimmingg: 0,
    minecart: 0,
    minecartg: 0,
    horse: 0,
    horseg: 0,
    donkey: 0,
    pig: 0,
    strider: 0,
    break: 0,
    place: 0,
    plant: 0,
    harvest: 0,
    burn: 0,
    bonus: 0,
  };

  const plotObject = {
    world: "",
    location: "",
    total: 0,
    nonownerBlockbreak: 0,
    nonownerBlockharvest: 0,
  };

  Players.sort((a, b) => b.totalcorrected - a.totalcorrected);

  Plots.sort((a, b) => b.total - a.total);

  Players.forEach((player) => {
    const player_temp = Object.create(playerObject);

    player_temp.p_id = player.playeruuid || 0;
    player_temp.amount = player.totalcorrected || 0;
    player_temp.walking = player.playermove || 0;
    player_temp.flying = player.playermoveflying || 0;
    player_temp.break = player.blockbreak || 0;
    player_temp.place = player.blockplace || 0;
    player_temp.plant = player.blockplant || 0;
    player_temp.harvest = player.blockharvest || 0;
    player_temp.burn = player.totalburn || 0;
    player_temp.bonus = player.bonus || 0;
    player_temp.gliding = player.playermovegliding || 0;

    player_temp.swimming =
      (player.playermoveswimming || 0) +
      (player.playermoveswimminggliding || 0);

    player_temp.minecart =
      (player.playermovevehicleminecart || 0) +
      (player.playermovevehicleminecartgliding || 0);

    player_temp.horse =
      (player.playermovevehiclehorse || 0) +
      (player.playermovevehiclehorsegliding || 0);

    player_temp.donkey = player.playermovevehicledonkey || 0;
    player_temp.pig = player.playermovevehiclepig || 0;
    player_temp.strider = player.playermovevehiclestrider || 0;
    player_temp.url_skin =
      "https://minotar.net/body/" + player.playerid + "/100.png";
    player_temp.amount = player.totalcorrected || 0;

    const [PlayerName, initPlayerName] = useState([]);
    if (top_100 < 50) {
      const fetchData = async () => {
        const response = await fetch(
          "https://playerdb.co/api/player/minecraft/" + player.playeruuid
        );
        if (!response.ok) {
          throw new Error("Data coud not be fetched!");
        } else {
          return response.json();
        }
      };
      useEffect(() => {
        fetchData()
          .then((res) => {
            initPlayerName(res.data.player.username);
          })
          .catch((e) => {
            console.log(e.message);
          });
      });
      player_temp.p_name = PlayerName;
      top_100++;
    } else {
      player_temp.p_name = "";
    }

    playerRewards[pr] = player_temp;
    pr++;
  });

  Plots.forEach((plot) => {
    const plot_temp = Object.create(plotObject);
    plot_temp.location = plot.location;
    plot_temp.world = plot.world;
    plot_temp.amount = plot.total;
    plot_temp.nonownerBlockbreak = plot.nonownerBlockbreak;
    plot_temp.nonownerBlockharvest = plot.nonownerBlockharvest;
    plot_temp.nonownerBlockplace = plot.nonownerBlockplace;
    plot_temp.nonownerBlockplant = plot.nonownerBlockplant;
    plot_temp.nonownerPlayermove = plot.nonownerPlayermove;
    plot_temp.nonownerPlayermoveflying = plot.nonownerPlayermoveflying;
    plot_temp.nonownerPlayermoveswimming = plot.nonownerPlayermoveswimming;
    plot_temp.ownerBlockbreak = plot.ownerBlockbreak;
    plot_temp.ownerBlockharvest = plot.ownerBlockharvest;
    plot_temp.ownerBlockplace = plot.ownerBlockplace;
    plot_temp.ownerBlockplant = plot.ownerBlockplant;
    plot_temp.ownerPlayermove = plot.ownerPlayermove;
    plotRewards[plotr] = plot_temp;
    plotr++;
  });

  const burnObject = {
    playeruuid: "",
    burncapped: 0,
    emerald: 0,
    goldblock: 0,
    lapis: 0,
    other: 0,
    rottenflesh: 0,
    total: 0,
  };

  Burn.forEach((player) => {
    const burn_temp = Object.create(burnObject);
    burn_temp.playeruuid = player.playeruuid;
    burn_temp.burncapped = player.totalcapped;
    burnRewards[burnr] = burn_temp;
    burnr++;
  });

  burnRewards.sort((a, b) => b.burncapped - a.burncapped);

  console.log(burnRewards);
  console.log(Burn);

  //console.log(total_land_rewards + " ----- " + total_player_rewards);
  return (
    <Container fluid>
      <Row>
        <Col>
          <Row
            style={{
              backgroundColor: "rgb(6, 255, 255)",
              borderRadius: "0.75em",
              border: "solid",
              borderColor: "rgb(255, 65, 198, 0.5)",
              padding: "0em",
            }}
          >
            <Col
              style={{ fontSize: "2.5em", padding: "0em", fontWeight: "900" }}
            >
              Player Reward Points Details
            </Col>
            <Table variant="custom" responsive>
              <thead>
                <tr>
                  <th>Rank</th>
                  <th>Player</th>
                  <th>Total</th>
                  <th>Walking</th>
                  <th>Break</th>
                  <th>Place</th>
                  <th>Plant</th>
                  <th>Harvest</th>
                  <th>Swimming</th>

                  <th>Flying</th>
                  <th>Gliding</th>
                  <th>Minecart</th>

                  <th>Horse</th>
                  <th>Donkey</th>
                  <th>Pig</th>
                  <th>Strider</th>

                  <th style={{ whiteSpace: "nowrap" }}>Visit Bonus</th>
                </tr>
              </thead>
              <tbody>
                {[...playerRewards].slice(0, 50).map((item, idx) => {
                  return (
                    <tr key={idx + "rank"} style={{ verticalAlign: "middle" }}>
                      <td>{idx + 1}</td>
                      <td style={{ whiteSpace: "nowrap" }}>
                        {item.p_name}
                        <img
                          alt="Minecraft skin"
                          style={{ width: "16px" }}
                          src={
                            "https://minotar.net/body/" + item.p_id + "/100.png"
                          }
                        />
                      </td>
                      <td>{item.amount}</td>

                      <td>{item.walking}</td>
                      <td>{item.break}</td>
                      <td>{item.place}</td>
                      <td>{item.plant}</td>
                      <td>{item.harvest}</td>
                      <td>{item.swimming}</td>

                      <td>{item.flying}</td>
                      <td>{item.gliding}</td>
                      <td>{item.minecart}</td>

                      <td>{item.horse}</td>
                      <td>{item.donkey}</td>
                      <td>{item.pig}</td>
                      <td>{item.strider}</td>

                      <td>{item.bonus}</td>
                    </tr>
                  );
                })}
              </tbody>
            </Table>
          </Row>
        </Col>

        <Col>
          <Row
            style={{
              backgroundColor: "rgb(6, 255, 255)",
              borderRadius: "0.75em",
              border: "solid",
              borderColor: "rgb(255, 65, 198, 0.5)",
              padding: "0em",
            }}
          >
            <Col
              style={{
                fontSize: "2.5em",
                padding: "0em",
                fontWeight: "900",
                width: "500px",
              }}
            >
              Top 100 Plot Points Details
            </Col>
            <Table variant="custom" responsive>
              <thead>
                <tr>
                  <th>Rank</th>
                  <th>Location</th>
                  <th>World</th>
                  <th>Amount</th>
                  <th>Visitor Blockbreak</th>
                  <th>Visitor Harvest</th>
                  <th>Visitor BlockPlace</th>
                  <th>Visitor Plant</th>
                  <th>Visitor Move</th>
                  <th>Visitor Fly</th>
                  <th>Visitor Swim</th>
                  <th>Owner BlockBreak</th>
                  <th>Owner BlockHarvest</th>
                  <th>Owner Blockplace</th>
                  <th>Owner BlockPlant</th>
                  <th>Owner PlayerMove</th>
                </tr>
              </thead>
              <tbody>
                {[...plotRewards]
                  .sort((a, b) => b.amount - a.amount)
                  .slice(0, 100)
                  .map((item, idx) => {
                    //let amount = item[1].toLocalString();
                    return (
                      <tr
                        key={idx + "rank"}
                        style={{ verticalAlign: "middle" }}
                      >
                        <td>{idx + 1}</td>
                        <td>{item.location}</td>
                        <td style={{ whiteSpace: "nowrap" }}>{item.world}</td>
                        <td>
                          <NumberFormat
                            thousandSeparator={true}
                            displayType={"text"}
                            value={item.amount}
                            decimalScale={2}
                          />
                        </td>
                        <td>{item.nonownerBlockbreak}</td>
                        <td>{item.nonownerBlockharvest}</td>
                        <td>{item.nonownerBlockplace}</td>
                        <td>{item.nonownerBlockplant}</td>
                        <td>{item.nonownerPlayermove}</td>
                        <td>{item.nonownerPlayermoveflying}</td>
                        <td>{item.nonownerPlayermoveswimming}</td>
                        <td>{item.ownerBlockbreak}</td>
                        <td>{item.ownerBlockharvest}</td>
                        <td>{item.ownerBlockplace}</td>
                        <td>{item.ownerBlockplant}</td>
                        <td>{item.ownerPlayermove}</td>
                      </tr>
                    );
                  })}
              </tbody>
            </Table>
          </Row>
        </Col>

        <Col>
          <Row
            style={{
              backgroundColor: "rgb(6, 255, 255)",
              borderRadius: "0.75em",
              border: "solid",
              borderColor: "rgb(255, 65, 198, 0.5)",
              padding: "0em",
            }}
          >
            <Col
              style={{
                fontSize: "2.5em",
                padding: "0em",
                fontWeight: "900",
                width: "500px",
              }}
            >
              Lava Burn Details
            </Col>
            <Table variant="custom" responsive>
              <thead>
                <tr>
                  <th>Rank</th>
                  <th>Player</th>
                  <th>Total</th>
                  
                  <th>Other</th>
                  <th>RottenFlesh</th>
                  <th>Lapis</th>
                  <th>Gold Block</th>
                  <th>Emerald</th>
             
                 
                </tr>
              </thead>
              <tbody>
                {[...Burn]
                  .sort((a, b) => b.totalcapped - a.totalcapped)
                  .slice(0, 100)
                  .map((item, idx) => {
                    //let amount = item[1].toLocalString();
                    return (
                      <tr
                        key={idx + "rank"}
                        style={{ verticalAlign: "middle" }}
                      >
                       <td>{idx + 1}</td>
                      <td style={{ whiteSpace: "nowrap" }}>
                        {item.playeruuid}
                        <img
                          alt="Minecraft skin"
                          style={{ width: "16px" }}
                          src={
                            "https://minotar.net/body/" + item.playeruuid + "/100.png"
                          }
                        />
                      </td>
                        <td>{item.totalcapped}</td>
                        <td>{item.other}</td>
                        <td>{item.rottenflesh}</td>
                        <td>{item.lapislazuli}</td>
                        <td>{item.goldblock}</td>
                        <td>{item.emerald}</td>
                      
            
                 
                      </tr>
                    );
                  })}
              </tbody>
            </Table>
          </Row>
        </Col>
      </Row>
    </Container>
  );
}
