import { useEffect } from "react";
import '@neftyblocks/market';
import "bootstrap/dist/css/bootstrap.min.css";

import Container from "react-bootstrap/Container";
import Table from "react-bootstrap/Table";

import Row from "react-bootstrap/Row";

import Col from "react-bootstrap/Col";

import { run as runHolder } from "holderjs/holder";

import NumberFormat from "react-number-format";

import React, { useState } from "react";

import LiftiumPrice from "./components/LiftiumPrice";

import MinerPrice from "./components/MinerPrice";

import "./pages.css";

export default function LiquidityLeaders() {
  let tempArray = [];
  const [liftLeaders, initLiftLeaders] = useState([]);
  const [liftLeaders2, initLiftLeaders2] = useState([]);
  const fetchData = async () => {
    const response = await fetch(
      "https://www.api.bloks.io/wax/tokens?type=topHolders&chain=wax&contract=token.nefty&symbol=NEFTY&limit=50"
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response.json();
    }
  };
  const fetchData2 = async () => {
    const response2 = await fetch(
      "https://www.api.bloks.io/wax/tokens?type=topHolders&chain=wax&contract=token.nefty&symbol=NEFTY&limit=50"
    );
    if (!response2.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response2.json();
    }
  };
  useEffect(() => {
    fetchData()
      .then((res) => {
    
        initLiftLeaders(res);
      })
      .catch((e) => {
        console.log(e.message);
      });
      fetchData2()
      .then((res2) => {
    
        initLiftLeaders2(res2);
      })
      .catch((e) => {
        console.log(e.message);
      });
  });

  useEffect(() => {
    runHolder("image-class-name");
  });


  console.log(liftLeaders);

  return (
    <Container fluid>
      <Container>
        <Row>

        <Col style={{
              margin: "auto",
              width: "35em",
              alignContent: "top",
              padding: "0",
              paddingRight:"0.1em",
              marginTop: "0",
            }}
            xl={1}>

          <Row style={{backgroundColor:"rgb(6, 255, 255)",borderRadius:"0.75em",border:"solid",borderColor:"rgb(255, 65, 198, 0.5)",padding:"0em"}}>
              <Col style={{fontSize:"1.5em",padding:"0em",fontWeight:"900"}}>
              LIFTNEF Leaders
              </Col>
            <Table variant="custom" responsive>
              <thead>
                <tr>
                  <th>#</th>
                  <th>Account</th>
                  <th>Amount</th>
                </tr>
              </thead>
              <tbody>
                {[...liftLeaders2].map((item, idx) => {
                    //let amount = item[1].toLocalString();
                    return (
                      <tr key={idx + "rank"}>
                        <td>{idx + 1}</td>
                        <td>{item[0].name}</td>
                        <td>
                          <NumberFormat
                            thousandSeparator={true}
                            displayType={"text"}
                            value={item[0].modified}
                            decimalScale={2}
                          />
                        </td>
                      </tr>
                    );
                  })}
              </tbody>
            </Table>
            </Row>
       
          </Col>
        

      </Row>
        <Row>
      
          <Col style={{
              margin: "auto",
              width: "35em",
              alignContent: "top",
              padding: "0",
              paddingRight:"0.1em",
              marginTop: "0",
            }}
            xl={1}>

          <Row style={{backgroundColor:"rgb(6, 255, 255)",borderRadius:"0.75em",border:"solid",borderColor:"rgb(255, 65, 198, 0.5)",padding:"0em"}}>
              <Col style={{fontSize:"1.5em",padding:"0em",fontWeight:"900"}}>
              NEFTY Leaders
              </Col>
            <Table variant="custom" responsive>
              <thead>
                <tr>
                  <th>#</th>
                  <th>Account</th>
                  <th>Amount</th>
                </tr>
              </thead>
              <tbody>
                {[...liftLeaders]
                  .sort((a, b) => a[0] - b[0])
                  .slice(0, 25)
                  .map((item, idx) => {
                    //let amount = item[1].toLocalString();
                    return (
                      <tr key={idx + "rank"}>
                        <td>{idx + 1}</td>
                        <td>{item[0]}</td>
                        <td>
                          <NumberFormat
                            thousandSeparator={true}
                            displayType={"text"}
                            value={item[1]}
                            decimalScale={2}
                          />
                        </td>
                      </tr>
                    );
                  })}
              </tbody>
            </Table>
            </Row>
       
          </Col>
        
   
        </Row>
      </Container>
    </Container>
  );
}
