import { useEffect } from "react";
import "@neftyblocks/market";
import Button from "react-bootstrap/Button";
import ToggleButton from "react-bootstrap/ToggleButton";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import Container from "react-bootstrap/Container";
import Table from "react-bootstrap/Table";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { run as runHolder } from "holderjs/holder";
import React, { useState } from "react";
import "./pages.css";
import "bootstrap/dist/css/bootstrap.min.css";

export default function MtLeaders() {
  const [liftLeaders, initLiftLeaders] = useState([]);
  const [statType, setStatType] = useState("xp");
  const [offset, setOffset] = useState(0);

  const handleClickTimePlayed = async () => {
    setStatType("played_time");
    const response = await fetch(
      "https://mt.metaverse.theuplift.world/highscore/api/highscore?start=" +
        offset +
        "&sort=played_time&order=desc",
      {
        method: "GET",
        mode: "cors",
      }
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      const result = await response.json();
      initLiftLeaders(result);
    }
  };

  const handleClickXP = async () => {
    setStatType("xp");
    const response = await fetch(
      "https://mt.metaverse.theuplift.world/highscore/api/highscore?start=" +
        offset +
        "&sort=xp&order=desc",
      {
        method: "GET",
        mode: "cors",
      }
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      const result = await response.json();
      initLiftLeaders(result);
    }
  };

  const handleClickDiggedNodes = async () => {
    setStatType("digged_nodes");
    const response = await fetch(
      "https://mt.metaverse.theuplift.world/highscore/api/highscore?start=" +
        offset +
        "&sort=digged_nodes&order=desc",
      {
        method: "GET",
        mode: "cors",
      }
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      const result = await response.json();
      initLiftLeaders(result);
    }
  };

  const handleClickDied = async () => {
    setStatType("died");
    const response = await fetch(
      "https://mt.metaverse.theuplift.world/highscore/api/highscore?start=" +
        offset +
        "&sort=died&order=desc",
      {
        method: "GET",
        mode: "cors",
      }
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      const result = await response.json();
      initLiftLeaders(result);
    }
  };

  const handleClickCrafted = async () => {
    setStatType("crafted");
    const response = await fetch(
      "https://mt.metaverse.theuplift.world/highscore/api/highscore?start=" +
        offset +
        "&sort=crafted&order=desc",
      {
        method: "GET",
        mode: "cors",
      }
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      const result = await response.json();
      initLiftLeaders(result);
    }
  };

  const handleClickInflictedDamage = async () => {
    setStatType("inflicted_damage");
    const response = await fetch(
      "https://mt.metaverse.theuplift.world/highscore/api/highscore?start=" +
        offset +
        "&sort=inflicted_damage&order=desc",
      {
        method: "GET",
        mode: "cors",
      }
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      const result = await response.json();
      initLiftLeaders(result);
    }
  };
  const handleNext = async () => {
    let tempOffset = offset;
    if (liftLeaders.length < 10) {
      tempOffset = offset;
    } else {
      tempOffset = tempOffset + 10;
    }
    setOffset(tempOffset);
    const response = await fetch(
      "https://mt.metaverse.theuplift.world/highscore/api/highscore?start=" +
        tempOffset +
        "&sort=" +
        statType +
        "&order=desc",
      {
        method: "GET",
        mode: "cors",
      }
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      const result = await response.json();
      initLiftLeaders(result);
    }
  };

  const handlePrevious = async () => {
    let tempOffset = offset;
    if (tempOffset === 0) {
      tempOffset = 0;
    } else {
      tempOffset = tempOffset - 10;
    }
    setOffset(tempOffset);

    const response = await fetch(
      "https://mt.metaverse.theuplift.world/highscore/api/highscore?start=" +
        tempOffset +
        "&sort=" +
        statType +
        "&order=desc",
      {
        method: "GET",
        mode: "cors",
      }
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      const result = await response.json();
      initLiftLeaders(result);
    }
  };

  const fetchData = async () => {
    const response = await fetch(
      "https://mt.metaverse.theuplift.world/highscore/api/highscore?start=0&sort=" +
        statType +
        "&order=desc",
      {
        method: "GET",
        mode: "cors",
      }
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response.json();
    }
  };

  const fetchRanks = async () => {
    const response = await fetch(
      "https://mt.metaverse.theuplift.world/highscore/api/ranks",
      {
        method: "GET",
        mode: "cors",
      }
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response.json();
    }
  };

  const fetchSkins = async () => {
    const response = await fetch(
      "https://mt.metaverse.theuplift.world/highscore/api/skinsdb",
      {
        method: "GET",
        mode: "cors",
      }
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response.json();
    }
  };

  useEffect(() => {
    fetchData()
      .then((res) => {
        initLiftLeaders(res);
      })
      .catch((e) => {
        console.log(e.message);
      });
    fetchRanks()
      .then((res) => {
        //console.log(res);
      })
      .catch((e) => {
        console.log(e.message);
      });

    fetchSkins()
      .then((res) => {
        //console.log(res);
      })
      .catch((e) => {
        console.log(e.message);
      });
    runHolder("image-class-name");
  }, []);
  const [checked, setChecked] = useState(false);
  const [radioValue, setRadioValue] = useState("1");

  const radios = [
    { name: "Active", value: "1" },
    { name: "Radio", value: "2" },
    { name: "Radio", value: "3" },
  ];

  return (
    <Container fluid>
      <Container>
        <Row>
          <div>
           
            <ButtonGroup
              style={{
                backgroundColor: "rgb(255, 65, 198, 0.5)",
                color: "white",
              }}
            >
              <Button
                variant="primary"
                size="sm"
                onClick={handleClickTimePlayed}
                style={{
                  backgroundColor: "rgb(255, 65, 198, 0.5)",
                  color: "white",
                  borderColor: "white",
                }}
              >
                PlayTime
              </Button>
              <Button
                size="sm"
                onClick={handleClickXP}
                style={{
                  backgroundColor: "rgb(255, 65, 198, 0.5)",
                  color: "white",
                  borderColor: "white",
                }}
              >
                XP
              </Button>
              <Button
                variant="primary"
                size="sm"
                onClick={handleClickDiggedNodes}
                style={{
                  backgroundColor: "rgb(255, 65, 198, 0.5)",
                  color: "white",
                  borderColor: "white",
                }}
              >
                Blocks Dug
              </Button>
              <Button
                variant="primary"
                size="sm"
                onClick={handleClickInflictedDamage}
                style={{
                  backgroundColor: "rgb(255, 65, 198, 0.5)",
                  color: "white",
                  borderColor: "white",
                }}
              >
                Inflicted Damage
              </Button>
              <Button
                variant="primary"
                size="sm"
                onClick={handleClickCrafted}
                style={{
                  backgroundColor: "rgb(255, 65, 198, 0.5)",
                  color: "white",
                  borderColor: "white",
                }}
              >
                Crafted
              </Button>
              <Button
                variant="primary"
                size="sm"
                onClick={handleClickDied}
                style={{
                  backgroundColor: "rgb(255, 65, 198, 0.5)",
                  color: "white",
                  borderColor: "white",
                }}
              >
                Died
              </Button>
            </ButtonGroup>
          </div>
        </Row>
        <Row>
          <Col
            style={{
              margin: "auto",
              width: "44em",
              alignContent: "top",
              padding: "0",
              paddingRight: "0.1em",
              marginTop: "0",
            }}
            xl={1}
          >
            <Row
              style={{
                backgroundColor: "rgb(6, 255, 255)",
                borderRadius: "0.75em",
                border: "solid",
                borderColor: "rgb(255, 65, 198, 0.5)",
                padding: "0em",
              }}
            >
              <Col
                style={{ fontSize: "1.5em", padding: "0em", fontWeight: "900" }}
              >
                MineTest Leaders
              </Col>
              <Table variant="custom" responsive>
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Name</th>
                    <th>PlayTime</th>
                    <th>XP</th>
                    <th>Blocks Dug</th>
                    <th>Inflicted Damage</th>
                    <th>Crafted</th>
                    <th>Died</th>
                  </tr>
                </thead>
                <tbody>
                  {[...liftLeaders].slice(0, 10).map((item, idx) => {
                    console.log(item.modified);
                    const then = Date.parse(item.modified);
                    const now = new Date();

                    const msBetweenDates = Math.abs(then - now);
                    let isOnline = "rgb(255, 65, 198, 0.5)";
                    // 👇️ convert ms to hours                  min  sec   ms
                    const minutesBetweenDates = msBetweenDates / (60 * 1000);
                    if (minutesBetweenDates < 10) {
                      isOnline = "rgb(255, 65, 198, 0.5)";
                    }
                    console.log(isOnline);
                    console.log(minutesBetweenDates);

                    return (
                      <tr
                        key={idx + "rank"}
                        style={{
                          backgroundColor:
                            minutesBetweenDates < 10
                              ? "rgb(6, 255, 255,0.3)"
                              : "rgb(255, 65, 198, 0.5)",
                          fontSize: minutesBetweenDates < 10 ? "1.3em" : "1em",
                        }}
                      >
                        <td>{idx + 1 + offset}</td>
                        <td>{item.name}</td>
                        <td>{item.attributes.played_time}</td>
                        <td>{item.attributes.xp}</td>
                        <td>{item.attributes.digged_nodes}</td>
                        <td>{item.attributes.inflicted_damage}</td>
                        <td>{item.attributes.crafted}</td>
                        <td>{item.attributes.died}</td>
                      </tr>
                    );
                  })}
                  <tr
                    style={{
                      backgroundColor: "rgb(6, 255, 255,0.3)",
                      fontSize: "1.3em",
                    }}
                  >
                    <td colSpan={8}>
                      <span>Online in the Last 10 Minutes</span>
                    </td>
                  </tr>
                </tbody>
              </Table>
            </Row>
          </Col>
        </Row>
        <Row>
          <Col>
            <ButtonGroup>
              <Button variant="secondary" size="sm" onClick={handlePrevious}>
                Previous
              </Button>
              <Button variant="secondary" size="sm" onClick={handleNext}>
                Next
              </Button>
            </ButtonGroup>
          </Col>
        </Row>
      </Container>
    </Container>
  );
}
