import { useEffect } from "react";
import Upliftium from "./upliftium";

import NeftyCard from "./components/NeftyCard";

import MioPrice from "./components/MioPrice";
import LiftiumPrice from "./components/LiftiumPrice";
import MinerPrice from "./components/MinerPrice";

import Container from "react-bootstrap/Container";

import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import { run as runHolder } from "holderjs/holder";

import React from "react";

import "./pages.css";
import "./components/cards.css";

export default function Stats() {
  // console.log(Upliftium.data);

  useEffect(() => {
    runHolder("image-class-name");
  });
  return (
    <Container style={{padding: "0em" }} fluid>
       <Row style={{paddingLeft: "10%",paddingRight:"10%" }} >
        <Col style={{ margin: "auto"}}>
          <MioPrice />
        </Col>
      </Row>

      
       <Container style={{padding: "0.5em" }} fluid>
     
      <Row>
        <Col style={{ margin: "auto" }}>
          <NeftyCard />
        </Col>
      </Row>
      </Container>
      
    </Container>
  );
}
