import { useCallback, useEffect, useMemo } from "react";

import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";

import Form from "react-bootstrap/Form";
import InputGroup from "react-bootstrap/InputGroup";

import Dropdown from "react-bootstrap/Dropdown";

import Container from "react-bootstrap/Container";

import Row from "react-bootstrap/Row";

import Col from "react-bootstrap/Col";

import React, { useState } from "react";

import "bootstrap/dist/css/bootstrap.min.css";
import "./pages.css";

export default function StoreHome() {
  const [storeItems, setStoreItems] = useState([]);
  const [searchItems, setSearchItems] = useState([]);
  const searchTerm = "";

  const SortItemsHighToLow = async () => {
    let sorted = [...storeItems]
      .slice(0)
      .sort((a, b) => b.item1_price.v - a.item1_price.v);

    setStoreItems(sorted);
  };

  const SortItemsLowToHigh = async () => {
    let sorted = [...storeItems]
      .slice(0)
      .sort((a, b) => a.item1_price.v - b.item1_price.v);

    setStoreItems(sorted);
  };

  const SortItemsServer = async () => {
    let sorted = [...storeItems]
      .slice(0)
      .sort((a, b) => a.server.v.localeCompare(b.server.v));

    setStoreItems(sorted);
  };

  const SortItemsByType = async () => {
    let sorted = [...storeItems]
      .slice(0)
      .sort((a, b) => a.item1.v.localeCompare(b.item1.v));

    setStoreItems(sorted);
  };

  const SearchItems = async () => {
    let foundItems = [];

    let n = 0;

    [...storeItems].forEach((item) => {
      if (item.item1.v.toLowerCase().includes(message.toLowerCase())) {
        //console.log("Found");
        foundItems[n] = item;
        n++;
      }
    });

    setStoreItems(foundItems);
    console.log("Input from search = " + message);
  };

  const ResetSearch = async () => {
    window.location.reload(false);
  };

  const fetchData = async () => {
    const response = await fetch(
      "https://docs.google.com/spreadsheets/d/1bTNqLhUapVTHsxpwluxM9K8T2R9KRNc5vj_3E4glRQw/gviz/tq?"
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response;
    }
  };

  useEffect(() => {
    fetchData()
      .then((res) => res.text())
      .then((rep) => {
        const data = JSON.parse(rep.substring(47).slice(0, -2));
        let storeItemArray = [];

        const storeObject = {
          store: "",

          owner: "",
          server: "",
          coordinates: "",
          item1: "",
          item1_pic: "",
          item1_price: 0,
          item2: "",
          item2_pic: "",
          item2_price: "",
          item3: "",
          item3_pic: "",
          item3_price: "",
          last_update: "",
        };

        data.table.rows.forEach((el, n) => {
          let item_temp = Object.create(storeObject);

          el.c.forEach((itemdata, i) => {
            if (i === 0) {
              item_temp.store = itemdata;
            }
            if (i === 1) {
              item_temp.owner = itemdata;
            }
            if (i === 2) {
              item_temp.server = itemdata;
            }
            if (i === 3) {
              item_temp.coordinates = itemdata;
            }
            if (i === 4) {
              item_temp.item1 = itemdata;
            }
            if (i === 5) {
              item_temp.item1_pic = itemdata;
            }
            if (i === 6) {
              item_temp.item1_price = itemdata;
            }
            if (i === 7) {
              item_temp.last_update = itemdata;
            }
          });

          storeItemArray[n] = item_temp;
        });

        setStoreItems(storeItemArray);
      })
      .catch((e) => {
        console.log(e.message);
      });
  }, []);

  const [message, setMessage] = useState("");

  const handleChange = (event) => {
    setMessage(event.target.value);

    console.log("value is:", event.target.value);
  };

  return (
    <Container fluid>
      <Dropdown>
        <Dropdown.Toggle
          style={{
            backgroundColor: " rgb(33, 0, 81, 0.9)",
            borderColor: "rgb(6, 255, 255)",
          }}
        >
          Choose Sorting Method
        </Dropdown.Toggle>

        <Dropdown.Menu renderOnMount="true">
          <Dropdown.Item onClick={() => SortItemsByType()}>
            By Block Type
          </Dropdown.Item>
          <Dropdown.Item onClick={() => SortItemsHighToLow()}>
            Price(high to low)
          </Dropdown.Item>
          <Dropdown.Item onClick={() => SortItemsLowToHigh()}>
            Price(low to high)
          </Dropdown.Item>
          <Dropdown.Item onClick={() => SortItemsServer()}>
            By Server
          </Dropdown.Item>
        </Dropdown.Menu>

        <Container fluid style={{ width: "35em" }}>
          <InputGroup className="mb-3">
            <Button
              variant="outline-secondary"
              id="search-btn"
              value="Search for Item"
              onClick={() => SearchItems()}
            >
              Search
            </Button>
            <Button
              variant="outline-secondary"
              id="search-btn"
              value="refresh search"
              onClick={() => ResetSearch()}
            >
              Refresh
            </Button>
            <Form.Control
              aria-label="Example text with button addon"
              aria-describedby="basic-addon1"
              type="text"
              id="message"
              name="message"
              onChange={handleChange}
              value={message}
            />
          </InputGroup>
        </Container>
      </Dropdown>
      <Container>
        <Row>
          {[...storeItems].map((item, idx) => {
            return (
              <Col key={idx}>
                <div>
                  <Card
                    style={{
                      width: "18em",
                      height: "14em",
                      alignContent: "center",
                      alignItems: "center",
                      backgroundColor: "rgb(255,255,255,1)",
                    }}
                  >
                    <Card.Img
                      variant="top"
                      src={item.item1_pic.v}
                      alt="Minecraft item"
                      style={{ width: "4em" }}
                    />
                    <Card.Body>
                      <div id="item-price">
                        {item.item1.v} : {item.item1_price.v} Upliftium
                      </div>

                      <div>Owner Name: {item.owner.v}</div>
                      <div>Server: {item.server.v}</div>
                      <div>Coordinates:{item.coordinates.v}</div>
                      <div>{item.last_update.v}</div>
                    </Card.Body>
                  </Card>
                </div>
              </Col>
            );
          })}
        </Row>
      </Container>
    </Container>
  );
}
