import Leaders from "./rewards/dripleaders";
import { useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";

import Container from "react-bootstrap/Container";

import Table from "react-bootstrap/Table";

import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import NumberFormat from "react-number-format";

import React, { useState } from "react";

import Ratio from "react-bootstrap/Ratio";

import "./pages.css";

export default function dripLeaders() {
  let landRewards = [];
  let playerRewards = [];
  let pr = 0;
  let lr = 0;
  let top_100 = 0;
  let total_land_rewards = 0;
  let total_player_rewards = 0;
  let top_100_land = 0;

  const playerObject = {
    account: "",
    p_id: "",
    p_name: "",
    amount: 0,
    type: "",
    url_skin: "",
  };

  console.log(Leaders);
  Leaders.data.payload.forEach((player) => {
    const player_temp = Object.create(playerObject);

    console.log(player.minecraftUUID);

    const [PlayerName, initPlayerName] = useState([]);

      const fetchData = async () => {
        const response = await fetch(
          "https://playerdb.co/api/player/minecraft/" + player.minecraftUUID
        );
        if (!response.ok) {
          throw new Error("Data coud not be fetched!");
        } else {
          return response.json();
        }
      };
      useEffect(() => {
        fetchData()
          .then((res) => {
            initPlayerName(res.data.player.username);
   
          })
          .catch((e) => {
            console.log(e.message);
          });
      });

      player_temp.p_name = PlayerName;
  


    player_temp.p_id = player.minecractUUID;
    player_temp.url_skin = "https://minotar.net/body/"+player.minecraftUUID+"/100.png";
    player_temp.amount = player.dripPerHour;
     playerRewards[pr] = player_temp;
     pr++;

  });

  return (
    <Container fluid>
      <Row>
      <Col>
          <Row
            style={{
              backgroundColor: "rgb(6, 255, 255)",
              borderRadius: "0.75em",
              border: "solid",
              borderColor: "rgb(255, 65, 198, 0.5)",
              padding: "0em",
            }}
          >
            <Col
              style={{ fontSize: "2.5em", padding: "0em", fontWeight: "900" }}
            >
             Drip Leaders
            </Col>
            <Table variant="custom" responsive>
              <thead></thead>

              <thead>
                <tr>
                  <th>Rank</th>
                  <th>Minecraft Name</th>
                  <th>Amount</th>
                </tr>
              </thead>
              <tbody  style={{fontSize:"1.5em"}}>
                {[...playerRewards].slice(0, 100).map((item, idx) => {
                  return (
                    <tr key={idx + "rank"} style={{ verticalAlign: "middle" }}>
                      <td>{idx + 1}</td>
                
                      <td>
                        {item.p_name}{" "}
                        <img
                          alt="Minecraft skin"
                          style={{ width: "16px" }}
                          src={item.url_skin}
                        />
                      </td>
                      <td>
                        <NumberFormat
                          thousandSeparator={true}
                          displayType={"text"}
                          value={item.amount}
                          decimalScale={2}
                        />
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </Table>
          </Row>
        </Col>
     
      </Row>
    </Container>
  );
}
