import { useEffect } from "react";
import "@neftyblocks/market";
import "bootstrap/dist/css/bootstrap.min.css";

import Container from "react-bootstrap/Container";
import Table from "react-bootstrap/Table";
import Orders from "./rewards/orders";

import Row from "react-bootstrap/Row";

import Col from "react-bootstrap/Col";

import { run as runHolder } from "holderjs/holder";

import React, { useState } from "react";

import "./pages.css";

export default function LandKeyLeaders() {
  const [landkeyLeaders, initKeyLeaders] = useState([]);
  const [shardLeaders, initShardLeaders] = useState([]);

  let tempArray = [];

  const holderObject = {
    account: "",
    keys: 0,
    shards: 0,
    finalkeys: 0,
  };

  const fetchShards = async () => {
    const response = await fetch(
      "https://wax.api.atomicassets.io/atomicassets/v1/accounts?collection_name=upliftworld&schema_name=keys&template_id=477759&page=1&limit=4000&order=desc"
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response.json();
    }
  };

  const fetchKeys = async () => {
    const response2 = await fetch(
      "https://wax.api.atomicassets.io/atomicassets/v1/accounts?collection_name=upliftworld&schema_name=keys&page=1&limit=4000&order=desc"
    );
    if (!response2.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response2.json();
    }
  };

  useEffect(() => {
    fetchShards()
      .then((res) => {
        initShardLeaders(res.data);
      })
      .catch((e) => {
        console.log(e.message);
      });

    fetchKeys()
      .then((res2) => {
        initKeyLeaders(res2.data);
      })
      .catch((e) => {
        console.log(e.message);
      });
  }, []);

  useEffect(() => {
    runHolder("image-class-name");
  });

  let finalCount = 0;

  landkeyLeaders.forEach((holder, index) => {
    const holder_temp = Object.create(holderObject);
    holder_temp.account = holder.account;

    holder_temp.keys = parseInt(holder.assets);

    for (var i = 0; i < shardLeaders.length; i++) {
      if (shardLeaders[i].account === holder.account) {
        //console.log("Found");

        holder_temp.shards = parseInt(shardLeaders[i].assets);

        //console.log(landkeyLeaders[index])
      }
    }
    holder_temp.finalkeys = holder_temp.keys - holder_temp.shards;
    tempArray[finalCount] = holder_temp;
    finalCount++;
  });
  if (tempArray.length != 0) {
    console.log(tempArray);
    tempArray.sort((a, b) => b.finalkeys - a.finalkeys);
  }

  return (
    <Container fluid>
      <Container>
        <Row>
          <Col
            style={{
              margin: "auto",
              width: "29em",
              alignContent: "top",
              padding: "0",
              paddingRight: "0em",
              marginTop: "0.5em",
            }}
            xl={1}
          >
            <Row
              style={{
                backgroundColor: "rgb(6, 255, 255)",
                borderRadius: "0.75em",
                border: "solid",
                borderColor: "rgb(255, 65, 198, 0.5)",
                padding: "0em",
              }}
            >
              <Col
                style={{ fontSize: "1.5em", padding: "0em", fontWeight: "900" }}
              >
                WAX Land Key Leaders
                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                 (Not including uplifted rplanet lands)
                </div>
              </Col>
              <Table variant="custom" responsive>
                <thead>
                  <tr>
                    <th>Account</th>
                    <th>Shards</th>
                    <th>Land Keys</th>
                  </tr>
                </thead>
                <tbody>
                  {tempArray.map((item, idx) => {
                    //let amount = item[1].toLocalString();
                    return (
                      <tr key={idx + "rank"}>
                        <td> <a
                        href={
                          "https://wax.atomichub.io/profile/" +
                          item.account +
                          "?collection_name=upliftworld&order=desc&sort=transferred"
                        }
                        target="_blank"rel="noreferrer"
                      >
                        {item.account}
                      </a>
                        </td>
                        <td>{item.shards}</td>
                        <td>{item.finalkeys}</td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
            </Row>
          </Col>

          <Col
            style={{
              margin: "auto",
              width: "30em",
              alignContent: "top",
              padding: "0",
              paddingRight: "0.1em",
              marginTop: "0.5em",
            }}
            xl={1}
          >
            <Row
              style={{
                backgroundColor: "rgb(6, 255, 255)",
                borderRadius: "0.75em",
                border: "solid",
                borderColor: "rgb(255, 65, 198, 0.5)",
                padding: "0em",
              }}
            >
              <Col
                style={{ fontSize: "1.5em", padding: "0em", fontWeight: "900" }}
              >
                <div
                  style={{
                    fontSize: "1.2em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  How to buy a NEW wax plot ...
                </div>
                <div
                  style={{
                    fontSize: "1.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  Go to{" "}
                  <a
                    href={"https://maps.theuplift.world/"}
                    target="_blank"
                    rel="noreferrer"
                    style={{
                      fontSize: "1em",
                      padding: "0em",
                      fontWeight: "900",
                      color: "rgb(255, 65, 198, 0.5)",
                    }}
                  >
                    World Map
                  </a>
                </div>
                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  Toggle the 'Plots' layer to show borders
                </div>

                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  <img
                    alt="toggle plot borders"
                    style={{
                      width: "20em",
                      borderRadius: "20px",
                      padding: "0.5em",
                    }}
                    src={require("./img/Screenshot 2022-09-17 062852.jpg")}
                  />
                </div>
                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  Find any blue border plot owned by 'Upliftservic'
                </div>
                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  <img
                    alt="toggle plot borders"
                    style={{
                      width: "20em",
                      borderRadius: "20px",
                      padding: "0.5em",
                    }}
                    src={require("./img/Screenshot 2022-09-17 062958.jpg")}
                  />
                </div>

                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  Click the link to go to listing at Atomichub
                </div>
                <div
                  style={{
                    fontSize: "0.8em",
                    padding: "0em",
                    fontWeight: "700",
                  }}
                >
                  <img
                    alt="toggle plot borders"
                    style={{
                      width: "20em",
                      borderRadius: "20px",
                      padding: "0.5em",
                    }}
                    src={require("./img/Screenshot 2022-09-17 063059.jpg")}
                  />
                </div>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
    </Container>
  );
}
